import React, { Component } from 'react';
import { injectGlobal } from 'styled-components'
import axios from 'axios';
import {
  Card,
  Box,
  BackgroundImage,
  Subhead,
  Small,
  Switch,
  Provider
} from 'rebass'
import flag_CA from './flag_CA.png'
import flag_US from './flag_US.png'
import './App.css';

injectGlobal`
  * { box-sizing: border-box; }
  body { margin: 0; }
`

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      list: [],
      search: '',
      showOnlyNocontact: false
    }
  }
  componentDidMount() {
    axios.get('/api/list')
      .then(res => {
        console.log("res", res);
        const list = res.data;
        // const posts = res.data.data.children.map(obj => obj.data);
        this.setState({ list });
      });
  }
  render() {
    const { list, search, showOnlyNocontact } = this.state;
    // const filtered = list.filter(inst => inst.SFSchoolName.includes(search) || inst.SFSchoolName.includes(search))
    const filtered = showOnlyNocontact ? list.filter(inst => inst.nocontact === true) : list;
    return (
      <Provider className="App">
        <div>
          <label htmlFor="showOnlyNocontact">Show only No Contact Institutions</label>
          <Switch
            onClick={() => this.setState({ showOnlyNocontact: !showOnlyNocontact})}
            id="showOnlyNocontact"
            checked={showOnlyNocontact}
          />
        </div>
        {/*
        <BootstrapTable data={list} striped hover>
          <TableHeaderColumn isKey dataField='SFID'>Product ID</TableHeaderColumn>
          <TableHeaderColumn dataField='SFCID'>SFCID</TableHeaderColumn>
          <TableHeaderColumn dataField='nocontact'>nocontact</TableHeaderColumn>
          <TableHeaderColumn dataField='SFSchoolName'>SFSchoolName</TableHeaderColumn>
          <TableHeaderColumn dataField='SchoolCity'>SchoolCity</TableHeaderColumn>
        </BootstrapTable>
        */}
        <table>
          <tbody>
            {
              filtered.map((inst, i) => {
                return (
                  <tr key={`${inst['SFCID']}_${i}`}>
                    <td className="SFCID">{inst.SFCID}</td>
                    <td className="SFSchoolName">{inst.SFSchoolName}</td>
                    <td className="SchoolCity">{inst.SchoolCity}</td>
                    <td className="SchoolState">{inst.SchoolState}</td>
                    <td title="no contact" className="nocontact">
                      <input type="checkbox" checked={inst.nocontact} readOnly />
                    </td>
                    <td className="country" className={`flag_${inst.country}`}>
                      <img style={{ width: '32px', height: '32px' }}src={inst.country === 'US' ? flag_US : flag_CA } />
                    </td>
                  </tr>
                );
              })
            }
          </tbody>
        </table>
      </Provider>
    );
  }
}

export default App;



